<the-card class="main" :frame="[&quot;head&quot;, &quot;body&quot;, &quot;foot&quot;]"><template v-slot:head-left><div class="head-left font-size-16 font-weight-500 font-color-333333">学习记录统计</div></template><template v-slot:head-right><div class="head-right flex flex-row"><div class="hr-btns"><a-config-provider :auto-insert-space-in-button="false"><a-button class="hrb-btn btns-radius-04 default-btns-blue" v-for="(v, k) in [&quot;本日&quot;, &quot;本周&quot;, &quot;本月&quot;]" @click="typeSearch(k)" :type="btnType === k ? &quot;primary&quot; : &quot;default&quot;" :class="{ &quot;btn-color&quot;: btnType === k }">{{ ["本日", "本周", "本月"][k] }}</a-button></a-config-provider></div></div></template><template v-slot:body><a-spin :spinning="allLoading.record"><the-line-chart class="body" renderId="record-line-chart" :renderData="lineChartData" :renderTick="tickInterval"></the-line-chart></a-spin></template></the-card>